<template>
    <div class="m-share-tabs">
        <div class="u-tabs p-animation fadeInDown">
            <!-- <div class="u-tabs-btn">
                <i class="el-icon-arrow-left"></i>
            </div>
            <div class="u-tabs-btn right">
                <i class="el-icon-arrow-right"></i>
            </div> -->
            <div
                class="u-tab-item"
                v-for="item in body_types"
                :key="item.value"
                @click="clickTabs(item)"
                :class="{
                    active: item.value == active,
                }"
            >
                <!-- <img class="u-icon-img" :src="getThumbnail(item.label)" :alt="item.label" /> -->
                <span class="u-tabs-span">{{ item.label }}</span>
            </div>
            <div class="u-tabs-filter">
                <el-popover placement="bottom-end" trigger="click" width="90" v-model="filterOpen">
                    <div class="u-tabs-marks">
                        <el-checkbox-button v-model="star" class="u-star">精选</el-checkbox-button>
                        <el-checkbox-button v-model="price_type" class="u-star">免费</el-checkbox-button>
                        <el-checkbox-button v-model="filter_empty_images" class="u-star">不看无图片</el-checkbox-button>
                    </div>
                    <svg width="40" height="40" viewBox="0 0 40 40" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:slot="'reference'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><rect width="40" height="40" rx="20" fill="transparent"/><path d="M12.25 13.61C14.27 16.2 18 21 18 21v6c0 .55.45 1 1 1h2c.55 0 1-.45 1-1v-6s3.72-4.8 5.74-7.39a.998.998 0 00-.79-1.61H13.04c-.83 0-1.3.95-.79 1.61z" fill="tranparent"/></svg>
                </el-popover>
            </div>
            <div class="u-tabs-search">
                <el-input placeholder="请输入搜索内容" v-model="title" suffix-icon="el-icon-search" class="u-search" />
            </div>
        </div>
    </div>
</template>

<script>
import { __imgPath } from "@jx3box/jx3box-common/data/jx3box";
export default {
    name: "tabs",
    props: ["body_types", "active"],
    data: function () {
        return {
            star: false,
            price_type: false,
            filter_empty_images: true,
            title: "",
            filterOpen: false,
        };
    },
    computed: {
        params() {
            const _params = {};
            if (this.active) {
                _params.body_type = this.active;
            }
            if (this.star) {
                _params.star = 1;
            }
            if (this.title) {
                _params.title = this.title;
            }
            if (this.price_type) {
                _params.price_type = 0;
            }
            if (this.filter_empty_images) {
                _params.filter_empty_images = true;
            }
            return _params;
        },
    },
    methods: {
        //切换数据
        clickTabs({ value }) {
            // this.active = value;
            this.$emit("setActive", value);
        },
        test(v) {
            console.log(v);
        },
        getThumbnail: function (filename) {
            return __imgPath + "image/face/" + filename + ".jpg";
        },
    },
    watch: {
        params: {
            deep: true,
            immediate: true,
            handler: function (obj) {
                this.$emit("change", obj);
            },
        },
    },
};
</script>
